// src/pages/GameOverPage.js

import React, { useState, useEffect, useRef } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import CountryCard from '../components/CountryCard';
import { submitScore } from '../api/leaderboardApi';
import { gameHistoryService } from '../services/gameHistoryService';
import { userProfileService } from '../services/userProfileService';
import { useAuth } from '../contexts/AuthContext';
import './GameOverPage.css';
import '../components/Buttons.css';

function GameOverPage() {
  const location = useLocation();
  const navigate = useNavigate();
  const { currentUser } = useAuth();
  
  // Destructure new potential state variables from Battle Royale mode
  const { 
    score = 0, 
    message = 'Game Over!', 
    mode = 'unknown', // Get mode correctly
    // Classic mode specific:
    incorrectCountry, 
    userOrder, 
    correctOrder, 
    // Battle Royale specific:
    winnerName, 
    finalPlayersState 
  } = location.state || {};

  const [playerName, setPlayerName] = useState('');
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [error, setError] = useState('');
  const hasSaved = useRef(false);

  // Fetch user profile for potential nickname
  useEffect(() => {
    const fetchUserProfile = async () => {
      if (currentUser) {
        try {
          const profile = await userProfileService.getUserProfile(currentUser.uid);
          if (profile?.nickname) {
            setPlayerName(profile.nickname);
          }
        } catch (err) {
          console.error('Error fetching user profile:', err);
        }
      }
    };
    fetchUserProfile();
  }, [currentUser]);

  // Save game history (adjust score/data based on mode)
  useEffect(() => {
    const saveGameHistory = async () => {
      // Only save if logged in and not already saved
      if (!currentUser || hasSaved.current) return;
      
      // Determine score and countries based on mode
      let historyScore = 0;
      let historyCountries = [];

      if (mode === 'classic_population' || mode === 'cooperation') {
         // Classic/Coop: Use userOrder if available, score passed directly (minus 1?)
         historyScore = score - 1 >= 0 ? score - 1 : 0; // Ensure score isn't negative
         historyCountries = userOrder || [];
      } else if (mode === 'battleRoyale') {
         // Battle Royale: Score is the winner's final score (correct placements)
         historyScore = score; // Score is passed directly as winner's score
         // Save sortedCountries at the end? Or just winner/score?
         // Let's stick to score for now, as order isn't the main point here.
         // Maybe save the final list of active/inactive players?
         historyCountries = finalPlayersState ? finalPlayersState.map(p => ({ name: p.name, score: p.score, isActive: p.isActive })) : []; // Save player states
      }

      // Only save if there's a valid mode and some data to save
      if (mode !== 'unknown' && (historyScore > 0 || historyCountries.length > 0)) {
         // Check if we have actual country data or player data to save
         const hasMeaningfulData = historyCountries.length > 0 && (historyCountries[0].flagUrl || historyCountries[0].name);

         if (hasMeaningfulData || historyScore > 0) {
            try {
              console.log('Attempting to save game history:', { userId: currentUser.uid, mode, historyScore });
              await gameHistoryService.saveGame(
                currentUser.uid,
                mode, // Pass the correct mode
                historyScore, // Pass the determined score
                // Map countries only if it's classic/coop and userOrder exists
                (mode === 'classic_population' || mode === 'cooperation') && userOrder 
                  ? userOrder.map(country => ({ 
                      id: country.id, 
                      name: country.name, 
                      flagUrl: country.flagUrl 
                    }))
                  : null // Pass null or adjust payload for battle royale history
              );
              hasSaved.current = true;
              console.log('Game history saved successfully');
            } catch (err) {
              console.error('Error saving game history:', err);
            }
         } else {
            console.log('Skipping game history save: No meaningful data.');
         }
      }
    };

    saveGameHistory();
  // Dependencies: mode, score, userOrder, finalPlayersState, currentUser
  }, [currentUser, mode, score, userOrder, finalPlayersState]); 

  // Handle score submission (use correct score based on mode)
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!playerName.trim()) {
      setError('Please enter your name');
      return;
    }
    
    // Determine score to submit based on mode
    let submitScoreValue = 0;
    if (mode === 'classic_population' || mode === 'cooperation') {
      submitScoreValue = score - 1 >= 0 ? score - 1 : 0; 
    } else if (mode === 'battleRoyale') {
      submitScoreValue = score; // Use the passed winner's score
    }
    
    if (submitScoreValue <= 0) {
        setError('Only scores greater than 0 can be submitted.');
        return;
    }

    try {
      submitScore(playerName, submitScoreValue, mode); // Pass correct mode
      setIsSubmitted(true);
      setError(''); // Clear error on success
    } catch (err) {
      setError('Failed to submit score. Please try again.');
    }
  };

  // Function to determine the correct path for "Play Again"
  const getPlayAgainPath = () => {
    switch (mode) {
      case 'classic_population':
        return '/game/population';
      case 'cooperation':
        return '/game/population/cooperation'; // Go back to lobby
      case 'battleRoyale': // Added case for Battle Royale
        return '/game/population/battleroyale'; // Go back to lobby
      case 'classic_area': 
         return '/game/area'; 
      default:
        return '/'; 
    }
  };

  // Determine score display text based on mode
  const getScoreDisplay = () => {
    if (mode === 'classic_population' || mode === 'cooperation') {
      return `Your final score: ${score - 1 >= 0 ? score - 1 : 0}`;
    } else if (mode === 'battleRoyale') {
      return winnerName ? `${winnerName}'s winning score: ${score}` : `Final Score: ${score}`;
    } else {
      return `Final Score: ${score}`;
    }
  };

  return (
    <div className={`game-over-page game-over-${mode}`}> {/* Add mode class */}
      <h2>Game Over</h2>
      <p>{message}</p>
      <p>{getScoreDisplay()}</p>

      {/* Display final player standings for Battle Royale */}
      {mode === 'battleRoyale' && finalPlayersState && (
        <div className="final-player-standings">
          <h3>Final Standings:</h3>
          <ul>
            {finalPlayersState.sort((a, b) => (b.score || 0) - (a.score || 0)).map(player => (
              <li key={player.id} className={!player.isActive ? 'eliminated-player' : ''}>
                {player.name}: {player.score || 0} points {!player.isActive ? '(Eliminated)' : ''}
              </li>
            ))}
          </ul>
        </div>
      )}

      {/* Score Submission Form (conditional?) */}
      {/* Maybe only show for classic/coop or if score > 0? */}
      {!isSubmitted && (mode === 'classic_population' || mode === 'cooperation' || (mode === 'battleRoyale' && score > 0)) && (
        <form onSubmit={handleSubmit} className="score-submission">
          <div className="input-group">
            <label htmlFor="playerName">Enter your name for Leaderboard:</label>
            <input
              type="text"
              id="playerName"
              value={playerName}
              onChange={(e) => setPlayerName(e.target.value)}
              placeholder="Your name"
              maxLength={20}
              disabled={!currentUser} // Disable if not logged in?
            />
          </div>
          {!currentUser && <p className="info-text">Log in to submit your score!</p>}
          {error && <p className="error">{error}</p>}
          <button type="submit" className="button button-primary" disabled={!currentUser || !playerName.trim()}>
            Submit Score
          </button>
        </form>
      )}
      {isSubmitted && (
        <div className="submission-success">
          <p>Score submitted successfully!</p>
        </div>
      )}

      {/* Display for Classic Mode incorrect placement */}
      {(mode === 'classic_population' || mode === 'classic_area') && userOrder && incorrectCountry && (
        <div className="user-order">
          <h3>Your order was:</h3>
          <div className="country-list">
            {userOrder.map((country) => (
              <CountryCard
                key={country.id}
                country={country}
                isClickable={false} // Should not be clickable here
                highlight={country.id === incorrectCountry.id ? 'incorrect' : ''}
                mode={mode === 'classic_population' ? 'population' : 'area'} // Use correct compare mode
              />
            ))}
          </div>
        </div>
      )}
      {(mode === 'classic_population' || mode === 'classic_area') && correctOrder && incorrectCountry && (
        <div className="correct-order">
          <h3>The correct order was:</h3>
          <div className="country-list">
            {correctOrder.map((country) => (
              <CountryCard
                key={country.id}
                country={country}
                isClickable={false}
                highlight={country.id === incorrectCountry.id ? 'correct' : ''}
                mode={mode === 'classic_population' ? 'population' : 'area'}
              />
            ))}
          </div>
        </div>
      )}

      <div className="game-over-buttons">
        <button className="button button-primary" onClick={() => navigate(getPlayAgainPath())}>
          Play Again {mode === 'battleRoyale' || mode === 'cooperation' ? '(New Lobby)' : ''}
        </button>
        <button className="button button-secondary" onClick={() => navigate('/')}>
          Go to Home Page
        </button>
        <button className="button button-secondary" onClick={() => navigate('/leaderboard')}>
          View Leaderboard
        </button>
      </div>
    </div>
  );
}

export default GameOverPage;
