import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useSearchParams, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { userProfileService } from '../services/userProfileService';
import { lobbyService } from '../services/lobbyService';
import { cooperationGameService } from '../services/cooperationGameService';
import { battleRoyaleGameService } from '../services/battleRoyaleGameService';
import './GameLobby.css';

// Define game settings based on mode (moved outside component)
const gameSettings = {
  cooperation: {
    minPlayers: 2,
    maxPlayers: 4, // Assuming max 4 for cooperation
    service: cooperationGameService,
    name: 'Cooperation Mode'
  },
  battleRoyale: {
    minPlayers: 2,
    maxPlayers: 8,
    service: battleRoyaleGameService,
    name: 'Battle Royale Mode'
  }
};

// Function to extract game mode from URL path (can also be outside)
const getGameModeFromPath = (pathname) => {
  const lowerPath = pathname.toLowerCase(); // Convert path to lowercase
  if (lowerPath.includes('/cooperation')) return 'cooperation';
  if (lowerPath.includes('/battleroyale')) return 'battleRoyale'; // Check lowercase path
  return null; // Or a default mode
};

function GameLobby() {
  const { currentUser } = useAuth();
  const [lobbyId, setLobbyId] = useState('');
  const [players, setPlayers] = useState([]);
  const [isHost, setIsHost] = useState(false);
  const [error, setError] = useState('');
  const [copyStatus, setCopyStatus] = useState('');
  const [gameMode, setGameMode] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();
  const [searchParams] = useSearchParams();
  const currentLobbyId = useRef('');
  const currentPlayer = useRef(null);

  // Effect 1: Determine and set the game mode based on the URL path
  useEffect(() => {
    const mode = getGameModeFromPath(location.pathname);
    if (!mode) {
      console.error('Invalid game mode in URL path:', location.pathname);
      setError('Invalid game mode specified in URL.');
      // Optional: navigate away after a delay
    } else {
      setGameMode(mode);
      setError(''); // Clear previous errors if mode is valid
    }
  }, [location.pathname]); // Only depends on the path

  // Effect 2: Initialize lobby, join/create, and subscribe *after* gameMode is set
  useEffect(() => {
    // Wait for gameMode to be determined and user to be logged in
    if (!gameMode || !currentUser) {
      return; 
    }

    let unsubscribeFn = null;
    let isMounted = true; // Flag to prevent state updates on unmounted component

    const initializeLobby = async () => {
      try {
        let playerName = '';
        try {
          const profile = await userProfileService.getUserProfile(currentUser.uid);
          playerName = profile?.nickname || '';
        } catch (fetchProfileError) {
          console.error('Error fetching user profile:', fetchProfileError);
        }

        const lobbyParam = searchParams.get('lobby');
        let targetLobbyId = '';
        let playerInfo = {
          id: currentUser.uid,
          name: playerName || (lobbyParam ? `Player ${Math.floor(Math.random() * 100)}` : 'Player 1'),
          email: currentUser.email
        };
        currentPlayer.current = playerInfo;

        if (lobbyParam) { // Join Lobby
          targetLobbyId = lobbyParam;
          currentLobbyId.current = targetLobbyId;
          if (isMounted) {
             setLobbyId(targetLobbyId);
             setIsHost(false); // Assume joining means not host initially
          }
          try {
            await lobbyService.joinLobby(targetLobbyId, playerInfo);
          } catch (joinError) {
            console.error('Error joining lobby:', joinError);
            if (isMounted) setError(joinError.message || 'Failed to join lobby');
            // Optional: navigate away
            return; // Stop if join fails
          }
        } else { // Create Lobby
          targetLobbyId = Math.random().toString(36).substring(2, 8).toUpperCase();
          currentLobbyId.current = targetLobbyId;
          if (isMounted) {
              setLobbyId(targetLobbyId);
              setIsHost(true);
          }
          try {
            // Use the gameMode state which should be set by the first effect
            await lobbyService.createLobby(targetLobbyId, playerInfo, gameMode); 
          } catch (createError) {
            console.error('Error creating lobby:', createError);
            if (isMounted) setError(createError.message || 'Failed to create lobby');
             // Optional: navigate away
            return; // Stop if create fails
          }
        }

        // Subscribe to lobby changes
        unsubscribeFn = lobbyService.subscribeToLobby(targetLobbyId, (lobbyData) => {
          if (!isMounted) return; // Don't update state if unmounted
          
          if (lobbyData) {
            // Update players and host status from DB data
            setPlayers(lobbyData.players || []);
            setIsHost(lobbyData.players && lobbyData.players[0]?.id === currentUser.uid);

            // Optional: Log DB mode for debugging if needed, but don't update component state based on it here
            // const dbMode = lobbyData.gameMode;
            // if (dbMode && dbMode !== gameMode) {
            //      console.log(`Debug: DB mode (${dbMode}) differs from component mode (${gameMode})`);
            // }
            
            // Navigation logic: Use the component's current gameMode state
            if (lobbyData.status === 'playing') {
              if (gameMode) { // Use the state variable gameMode set by the first effect
                  const navigatePath = `/game/population/${gameMode}/play`;
                  try {
                    console.log(`Navigating to ${navigatePath} with mode ${gameMode}`);
                    navigate(navigatePath, { 
                      state: { lobbyId: targetLobbyId, players: lobbyData.players, gameMode: gameMode } // Pass current gameMode state
                    });
                  } catch (navError) {
                      console.error("Navigation error:", navError);
                  }
              } else {
                 // This should ideally not happen if Effect 1 ran correctly
                 console.error("Cannot navigate, gameMode state is null/undefined.");
                 setError("Error determining game mode for navigation.");
              }
            }
          } else {
            setError('Lobby not found or has been closed');
             // Optional: navigate away
          }
        });
      } catch (error) {
        console.error('Error initializing lobby:', error);
        if (isMounted) setError('Failed to initialize lobby');
      }
    };

    initializeLobby();

    // Cleanup function
    return () => {
      isMounted = false; // Set flag on unmount
      if (unsubscribeFn) {
        unsubscribeFn();
      }
      // Only attempt to leave if we successfully joined/created a lobby
      if (currentLobbyId.current && currentPlayer.current) {
        lobbyService.leaveLobby(currentLobbyId.current, currentPlayer.current.id);
      }
    };
    // This effect now depends on gameMode being set, user, and searchParams changes
  }, [gameMode, searchParams, currentUser, navigate]); 

  const copyLink = async () => {
    if (!gameMode || !currentLobbyId.current) return;

    const baseUrl = window.location.origin;
    const gameLink = `${baseUrl}/#/game/population/${gameMode}?lobby=${encodeURIComponent(currentLobbyId.current)}`;
    
    try {
      await navigator.clipboard.writeText(gameLink);
      setCopyStatus('Copied!');
      setTimeout(() => setCopyStatus(''), 2000);
    } catch (err) {
      const textArea = document.createElement('textarea');
      textArea.value = gameLink;
      textArea.style.position = 'fixed';
      textArea.style.left = '-9999px';
      document.body.appendChild(textArea);
      textArea.focus();
      textArea.select();
      try {
        document.execCommand('copy');
        setCopyStatus('Copied!');
        setTimeout(() => setCopyStatus(''), 2000);
      } catch (copyErr) {
        setCopyStatus('Failed to copy');
        console.error('Fallback copy failed: ', copyErr);
      }
      document.body.removeChild(textArea);
    }
  };

  const startGame = async () => {
    if (!isHost || !gameMode || !currentLobbyId.current) return;

    const settings = gameSettings[gameMode]; // Get settings here when needed
    if (!settings) {
      setError('Invalid game mode configuration.');
      return;
    }

    if (players.length < settings.minPlayers) {
      setError(`Waiting for more players. Need at least ${settings.minPlayers}.`);
      return;
    }
    if (players.length > settings.maxPlayers) {
      setError(`Too many players. Maximum is ${settings.maxPlayers}.`);
      return;
    }

    try {
      setError('');
      
      await settings.service.initializeGameState(
        currentLobbyId.current, 
        players 
      );

      await lobbyService.startGame(currentLobbyId.current);

    } catch (error) {
      console.error('Error starting game:', error);
      setError(error.message || 'Failed to start game');
    }
  };
  
  if (!gameMode) {
    return <div className="game-lobby">Determining game mode... {error && <p className="error-message">{error}</p>}</div>;
  }
  if (!currentUser) {
      return <div className="game-lobby">Please log in...</div>; // Or redirect logic
  }

  const settings = gameSettings[gameMode]; // Get settings here for rendering

  return (
    <div className="game-lobby">
      <h2>{settings?.name || 'Game Lobby'}</h2>
      {error && <p className="error-message">{error}</p>}
      <div className="lobby-info">
        <p>Lobby ID: <strong>{lobbyId}</strong></p>
        {isHost && (
          <button 
            className={`button button-secondary copy-button ${copyStatus ? 'copied' : ''}`} 
            onClick={copyLink}
            disabled={!lobbyId}
          >
            {copyStatus || 'Copy Invite Link'}
          </button>
        )}
      </div>

      <div className="player-list">
        <h3>Players ({players.length}/{settings?.maxPlayers || 'N/A'}):</h3>
        <ul>
          {players.map((player, index) => (
            <li key={player.id || index}>
              {player.name} {player.id === currentUser?.uid ? '(You)' : ''} {index === 0 ? '(Host)' : ''}
            </li>
          ))}
        </ul>
        {players.length < settings?.minPlayers && (
            <p className="waiting-message">Waiting for at least {settings.minPlayers} players...</p>
        )}
      </div>

      {isHost && (
        <button 
          className="button button-primary start-button" 
          onClick={startGame}
          disabled={players.length < settings?.minPlayers || players.length > settings?.maxPlayers} 
        >
          Start Game
        </button>
      )}
      {!isHost && <p>Waiting for the host to start the game...</p>}
    </div>
  );
}

export default GameLobby; 